import React, { Component } from 'react';
import '../css/GuitarFX.css';
import '../css/ApiApp.css';
import InputRange from 'react-input-range';
import 'react-input-range/lib/css/index.css';

class GuitarFX extends Component {
  constructor(props) {
    super(props);

    this.state = {
      volume: 50, // Initial volume value
      gain: 50,   // Initial gain value
      isComponentVisible: false,
    };
  }

  handleVolumeChange = (value) => {
    this.setState({ volume: value });
  }

  handleGainChange = (value) => {
    this.setState({ gain: value });
  }

  toggleComponent = () => {
    console.log('Toggle button clicked');
    this.setState(prevState => ({
      isComponentVisible: !prevState.isComponentVisible,
    }));
  };

  render() {
    const { isComponentVisible } = this.state;
    return (
      <div className='center-button'>
        <button id='toggleBtn' className='button-70' onClick={this.toggleComponent}>View Audio App</button>
        {this.state.isComponentVisible && (  
            <div className='wider-space'>
          <div className="guitar-fx short-slider">
            <div className="slider space">
              <label className='space'>Volume</label>
              <InputRange
                maxValue={100}
                minValue={0}
                value={this.state.volume}
                onChange={this.handleVolumeChange}
              />
            </div>
            <div className="slider space">
              <label className='space'>Gain</label>
              <InputRange
                maxValue={100}
                minValue={0}
                value={this.state.gain}
                onChange={this.handleGainChange}
              />
            </div>
          </div>
          </div>
        )}
      </div>
    );
  }
}

export default GuitarFX;
